export function rollItemsInArray(itemGroups, gossipScore, roll) {
  const availableItems = [];
  itemGroups.forEach((group) => {
    const availableGroupItems = [];
    const { label, key, groupItems } = group;
    groupItems.forEach((item) => {
      switch (item.availability) {
        case 1:
          if (gossipScore - 30 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 2:
          if (gossipScore - 20 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 3:
          if (gossipScore - 10 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 4:
          if (gossipScore >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 5:
          if (gossipScore + 10 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 6:
          if (gossipScore + 20 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        case 7:
          if (gossipScore + 30 >= roll) {
            availableGroupItems.push(item);
          }
          break;
        default:
          break;
      }
    });
    const filteredGroupObject = {
      key: key,
      label: label,
      availableGroupItems: availableGroupItems,
    };
    availableItems.push(filteredGroupObject);
  });
  return availableItems;
}

export const populationBrackets = [
  {
    label: "Below 100",
    value: "low",
  },
  {
    label: "Below 1,000",
    value: "average",
  },
  {
    label: "Below 10,000",
    value: "high",
  },
  {
    label: "10,000 or more",
    value: "vhigh",
  },
];
